export default [

  {
    path: '/404',
    meta: {
      public: true,
    },
    name: 'NotFound',
    component: () => import(
      `@/pages/NotFound.vue`
    )
  },
  {
    path: '/403',
    meta: {
      public: true,
    },
    name: 'AccessDenied',
    component: () => import(
      `@/pages/Deny.vue`
    )
  },
  {
    path: '/500',
    meta: {
      public: true,
    },
    name: 'ServerError',
    component: () => import(
      `@/pages/Error.vue`
    )
  },
  {
    path: '/login',
    meta: {
      public: true,
    },
    name: 'Login',
    component: () => import(
      `@/pages/Login.vue`
    )
  },
  {
    path: '/',
    meta: {
      public: true,
    },
    name: 'root',
    component: () => import(
      `@/pages/Login.vue`
    )
  },
  {
    path: '/dashboard',
    meta: { 
      requiresAuth: true,
      is_admin: true
    },
    name: 'Root',
    redirect: {
      name: 'Dashboard'
    }
  },
  {
    path: 'compras/administrar',
    meta: {
      breadcrumb: true
    },
    name: 'components/Compras_administrar',
    component: () => import(
      `@/components/Compras_administrar.vue`
    )
  },
  {
    path: 'compras/registro',
    meta: {
      breadcrumb: true
    },
    name: 'components/Compras_registro',
    component: () => import(
      `@/components/Compras_registro.vue`
    )
  },
  {
    path: 'ventas/registrar',
    meta: {
      breadcrumb: true
    },
    name: 'Venta_nueva',
    component: () => import(
      `@/components/Ventas_registrar.vue`
    )
  },
  {
    path: 'ventas/dia',
    meta: {
      breadcrumb: true
    },
    name: 'components/Ventas_dia',
    component: () => import(
      `@/components/Ventas_dia.vue`
    )
  },
  {
    path: 'ventas/mes',
    meta: {
      breadcrumb: true
    },
    name: 'components/Ventas_mes',
    component: () => import(
      `@/components/Ventas_mes.vue`
    )
  },
  {
    path: 'ventas/fecha',
    meta: {
      breadcrumb: true
    },
    name: 'components/Ventas_fecha',
    component: () => import(
      `@/components/Ventas_fecha.vue`
    )
  },
  {
    path: 'productos/administrar',
    meta: {
      breadcrumb: true
    },
    name: 'components/Producto_administrar',
    component: () => import(
      `@/components/Producto_administrar.vue`
    )
  },
  {
    path: 'Productos/registrar',
    meta: {
      breadcrumb: true
    },
    name: 'components/Producto_registrar',
    component: () => import(
      `@/components/Producto_registrar.vue`
    )
  },
  {
    path: 'usuarios/administrar',
    meta: {
      breadcrumb: true
    },
    name: 'components/Usuario_administrar',
    component: () => import(
      `@/components/Usuarios_administrar.vue`
    )
  },
  {
    path: 'usuarios/registrar',
    meta: {
      breadcrumb: true
    },
    name: 'components/Usuario_registrar',
    component: () => import(
      `@/components/Usuarios_registrar.vue`
    )
  },


  {
    path: '/dashboard',
    meta: { breadcrumb: true },
    name: 'Dashboard',
    component: () => import(
      `@/pages/Dashboard.vue`
    )
  }
];



